@import url(https://fonts.googleapis.com/css?family=Jost);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.homeHeader{
    width: 100%;
    height: 75vh;
    position: relative;
    overflow: hidden;
}

.homeHeaderContent{
    margin: 0 auto;
    width: 100%;
    background-image: url(/Abigail_Portfolio/static/media/homeBack1.d571598b.png);
    height: 100%;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
}

nav{
    padding: 1rem 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    z-index: 2;
    font-size: 1rem;
}

.navOptions{
    padding: 1rem 2rem;
    width: 100%;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    -webkit-tap-highlight-color:transparent;
}

.socailMedia{
    cursor: pointer;
    margin-right: 2rem;
    display: none;
}

.navOptions a{
    transition: all 0.3s ease-out;
    text-decoration: none;
    font-weight: 100;
    color: black;
}

.socailMedia i{
    padding-left: 2rem;
    transition: all 0.3s ease-out;
}

.navOptions a:hover, .navOptions a.activeNav, .socailMedia i:hover{
    font-weight: 700;
    color: white;
    text-decoration: underline;
}


.homeHeaderInfo{
    z-index: 2;
    margin: 0 2rem;
    margin-bottom: 40%;
}

.homeHeaderContentDes{
    color: rgba(250, 250, 250, 0.753);
    z-index: 1;
    font-size: 2vw;
}


.homeHeaderContentSelf{
    font-weight: 900;
    font-size: 12.2vw;
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.123);
    z-index: 1;
    line-height: 17vw;
}

.homeHeaderHello{
    display: block;
} 

.homeHeaderContentBack{
    background-color: rgb(49, 49, 49);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.3;
    z-index: 1;
}

.aboutMeButton{
    padding: 10px 2rem;
    font-size: 1rem;
    background:none;
    color: white;
    border-radius: 30px;
