    top: 2rem;
    text-decoration: none;
    -webkit-tap-highlight-color:transparent
}

.aboutMeButton:hover{
    background-color: white;
    color: black;
    outline: none;
}

.aboutMeButton:active{
    background-color: rgb(226, 226, 226);
    color: black;
    outline: none;
}
