.homeWorksPicutres{
    width: 75%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 2rem auto;
    grid-gap: 55px;
    max-width: 1000px;
}

.homeWorksPicutresImgItem{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    box-shadow: 10px 15px 20px rgba(243, 231, 209, 0.541);
    border-radius: 10px;
    background-color: rgb(194, 164, 48);
}

.homeWorksInfo{
    opacity: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.363);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    flex-direction: column;
    transition: all 0.3s ease-in-out;
    text-align: center;
    padding: 0 10px;
}

.onMouseOver .homeWorksInfo{
    opacity: 1;
}

.homeWorksPicutresImgItemTitle{
    margin-bottom: 0.4rem;
    font-size: 0.8rem;
}

.fas.fa-arrow-circle-right{
    font-size: 0.95rem;
}

.homeWorksPicutresImg{
    margin: 0 !important;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 3px;
}

.homeWorksPicutresImg.onMouseOver{
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.homeWorksViewMore{
    padding: 10px 2rem;
    font-size: 1rem;
    background:none;
    color: rgb(247, 193, 94);
    border-radius: 30px;
    border: 1px solid rgb(247, 193, 94);
    cursor: pointer;
    transition: all 0.3s ease-out;
    outline: none;
    margin-top: 1rem;
    text-decoration: none;
}

.homeWorksViewMore:hover{
    background-color: rgb(247, 193, 94);
    color: white;
    outline: none;
}

.homeWorksViewMore:active{
    background-color: rgb(214, 167, 81);
    color: white;
    outline: none;
}
.contactMe{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url(/Abigail_Portfolio/static/media/homeBack1.d571598b.png);
    background-position: top;
    background-size: cover;
    background-attachment: fixed;
    padding: 6rem 0 5rem 0;
}

.contactMeMain{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 85%;
    z-index: 30;
    
}

.contactMeTitle{
    font-weight: 700;
    font-size: 1.4rem;
    border-bottom: 3px solid rgb(255, 184, 53);
    margin-bottom: 3rem;
    color: white;
}

.contactMeBack{
    background-color: black;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.3;
    z-index: 1;
}

.contactMeForm{
    width: 100%;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.contactMeInput,.contactMeTextarea{
    font-size: 1rem;
    padding: 10px 1rem;
    border: none;
    background-color: rgba(255, 255, 255, 0.4);
    margin-bottom: 1.5rem;
    outline: none;
    transition: all 0.3s ease-in-out;
}

.contactMeInput:focus,.contactMeTextarea:focus{
    background-color: rgba(255, 255, 255, 0.9);
    color: black;
}

.contactMeInput:focus::-webkit-input-placeholder, .contactMeTextarea:focus::-webkit-input-placeholder{
    color: rgb(117, 117, 117);
}

.contactMeInput:focus:-ms-input-placeholder, .contactMeTextarea:focus:-ms-input-placeholder{
    color: rgb(117, 117, 117);
}

.contactMeInput:focus::-ms-input-placeholder, .contactMeTextarea:focus::-ms-input-placeholder{
    color: rgb(117, 117, 117);
}

.contactMeInput:focus::placeholder,.contactMeTextarea:focus::placeholder{
    color: rgb(117, 117, 117);
}

.contactMeTextarea{
    height: 6rem;
    font-size: 1rem;
    font-family: 'Jost' ,-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.contactMeInput::-webkit-input-placeholder, .contactMeTextarea::-webkit-input-placeholder{
    color: rgba(255, 255, 255, 0.8);
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    font-size: 1rem;
}

.contactMeInput:-ms-input-placeholder, .contactMeTextarea:-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.8);
    -ms-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    font-size: 1rem;
}

.contactMeInput::-ms-input-placeholder, .contactMeTextarea::-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.8);
    -ms-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    font-size: 1rem;
}

.contactMeInput::placeholder,.contactMeTextarea::placeholder{
    color: rgba(255, 255, 255, 0.8);
    transition: all 0.3s ease-in-out;
    font-size: 1rem;
}


.contactMeBtn{
    padding: 10px 4rem;
    font-size: 1rem;
    background:none;
    color: white !important;
    align-self: center;
    border-radius: 30px;
    border: 1px solid white;
    cursor: pointer;
    transition: all 0.3s ease-out;
    outline: none;
    text-decoration: none;
    margin-top: 1rem;
}

.contactMeBtn.loading{
    cursor: wait
}

.contactMeBtn:hover{
    background-color: white;
    color: black !important;
    outline: none;
}

.contactMeBtn:active{
    background-color: rgb(226, 226, 226);
    color: black;
    outline: none;
}
.homeMain{
    width: 90%;
    margin: 0 auto;
    padding: 5vh 0;
}

.Notification{
    position: fixed;
    top: 2rem;
    width: 16rem;
    left: calc((100% - 20rem)*0.5);
    background-color: white;
    border-radius: 5px;
    padding: 1rem 2rem;
    border-bottom: 5px solid rgb(61, 167, 61);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.226);
    z-index: 1000;
    transition: transform 0.5s ease-in;
}

.Notification.hide{
    transform: translateY(-300%) scale(0.6);
}

.NotificationTitle{
    font-size: 1.3rem;
    font-weight: 600;
    color: rgb(61, 167, 61);
}

.NotificationDes{
    color: rgb(114, 114, 114);
}


@media (min-width: 600px) {
    .homeHeaderContentSelf{
        font-size: 4rem;
        line-height: 5rem;
    }
    .homeHeaderInfo{
        margin: 0 3rem;
        margin-bottom: 13rem;
    }
    .navOptions{
        justify-content: flex-start;
        padding: 1rem 3rem;
        width: calc(100% - 6rem);
    }
    .navOptions a{
        padding-right: 3rem;
        font-size: 1.2rem;
    }

    .homeWorksCategoryItem{
        font-size: 1rem;
        padding: 5px 20px;
    }

    .homeWorksPicutresImgItemTitle{
        font-size: 1rem;
    }
    
    .fas.fa-arrow-circle-right{
        font-size: 1rem;
    }
}

@media (min-width: 1300px) {
    .navOptions a{
        color: rgba(255, 255, 255, 0.801);
        padding-right: 6rem;
    }
    
}
.WorkPage{
    width: 90%;
    margin: 0 auto;
    padding: 5vh 0; 
}

.workDetail{
    width: 100%;
    background-color: black;
    border-radius: 4px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.workDetail img{
    object-fit: contain;
    height: 63vh;
}

.workDetailVideo{
    object-fit: contain;
    height: 63vh;
    max-width: 100%;
}

.workContent{
    background-color: black;
    padding: 1rem;
    width: 100%;
    color: white;
    border-top: 1px solid rgba(255, 255, 255, 0.233);
}

.workTitle{
    font-size: 1rem;
    font-weight: 800 !important;
}

.workTime{
    font-size: 0.5rem;
    opacity: 0.5;
    font-weight: 300;
    margin-left: 1rem;
}
.workDiscreption{
    margin-top: 0.3rem;
    font-size: 0.8rem;
}

.far.fa-times-circle{
    position: fixed;
    top: 0.7rem;
    right: 0.7rem;
    color: white;
    font-size: 1.3rem;
}

@media (min-width: 600px) {
    .WorkPage .homeWorksPicutres{
        grid-template-columns: 1fr 1fr !important;
    }
}


@media (min-width: 1300px) {
    .WorkPage .homeWorksPicutres{
        grid-template-columns: 1fr 1fr 1fr !important;
    }
}
